<template>
    <div class="login-wrap">
        <div class="ms-title">留言板-欢迎登陆</div>
        <div class="ms-login">
            <el-form :model="ruleForm" :rules="rules" ref='ruleForm' label-width="0px" class="demo-ruleForm">
                <div v-if="errorInfo" style="margin-bottom: 5px">
                    <span>{{errInfo}}</span>
                </div>
                <el-form-item prop="username" >
                    <el-input v-model="ruleForm.username" placeholder="请输入用户名" @keyup.enter.native="submitForm('ruleForm')" ></el-input>
                </el-form-item>
                <el-form-item prop="password" >
                    <el-input v-model="ruleForm.password" type="password" placeholder="请输入密码" @keyup.enter.native="submitForm('ruleForm')"></el-input>
                </el-form-item>
                <el-form-item prop="validate">
                    <el-input v-model="ruleForm.validate" class="validate-code" pleaceholder="验证码" @keyup.enter.native="submitForm('ruleForm')"></el-input>
                    <div class="code" @click="refreshCode" >
                        <identify :identifyCode="identifyCode"></identify>
                    </div>
                </el-form-item>
                <div class="login-btn">
                    <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
                </div>
                <p class="register" @click="gotoRegister()">注册</p>
            </el-form>
        </div>
    </div>
</template>

<script>
    import identify from "@/components/identify"
    import api from "@/api/index"
    import {mapMutations} from "vuex";

    export default {
        components:{identify},
        data() {
            return {
                identifyCodes:"23456789ABCDEFGHIJKMNPQRSTUVWXYZabcdefghijkmnpqrstuvwxyz",
                identifyCode:"",
                errorInfo:false,
                ruleForm:{
                    username:'',
                    password:'',
                    validate:''
                },
                userToken:'',
                rules:{
                    username: [
                        {required:true,message:"请输入用户名",trigger:'blur'}
                    ],
                    password: [
                        {required:true,message:"请输入密码",trigger: 'blur'}
                    ],
                    validate:[
                        {required:true,message:"请输入验证码",trigger:'blur'}
                    ]
                }
            }
        },
        mounted(){
          this.identifyCode="",
          this.makeCode(this.identifyCodes,4)
        },
        methods: {
           ...mapMutations(['changeLogin']),
           submitForm(formName){
              // console.log('hello')
               //console.log(formName)
               const self=this;
               //console.log( self.$refs.formName)
               self.$refs[formName].validate(async (valid) => {
                   //console.log(123)
                   if(valid){
                       if(self.ruleForm.validate.toLowerCase()!=this.identifyCode.toLowerCase()){
                           self.errorInfo=true,
                           self.errInfo="验证码错误",
                           self.$message.error("验证码错误"),
                           this.refreshCode()
                       }else {
                              let user = {}
                              user["username"] = self.ruleForm.username
                              user["password"] = self.ruleForm.password
                              let validInfo = await api.loginUser(user)
                              console.log(validInfo)
                              if(validInfo.data.code==-1){
                                  self.errorInfo=true,
                                  self.errInfo=validInfo.data.message,
                                  self.$message.error(validInfo.data.message)
                                  this.refreshCode()
                              }
                              else if (validInfo.data.code==0){
                                  self.userToken=validInfo.data.data,
                                      // 将用户token保存到vuex中
                                  this.$store.commit('Authorization',self.userToken)
                                  //self.changeLogin({Authorization:self.userToken}),
                                  self.$message.success(validInfo.data.message),
                                  //localStorage.setItem("username", validInfo.config.data.username)
                                  await self.$router.push('/message')
                              }
                       }
                   }else{
                       console.log("error submit!");
                       return false;
                   }
               })
           },
            gotoRegister(){
                this.$router.push('/register')
            },
            randomNum(min,max){
                return Math.floor(Math.random()*(max-min)+min)
            },
            refreshCode(){
                this.identifyCode="",
                    this.makeCode(this.identifyCodes,4)
            },
            makeCode(o,l){
                for (let i=0;i<l;i++){
                    this.identifyCode+=this.identifyCodes[
                        this.randomNum(0,this.identifyCodes.length)
                        ]
                }
                console.log(this.identifyCode);
            }
        }
    }
</script>

